import React from 'react'
import styles from './Form.module.css';

export default function Form({ handleChange, handleSubmit }) {
  return (
    <form className={styles.nameForm} onSubmit={handleSubmit}>
      <h2 className={styles.question}>  ?</h2>
      <input type="text" onChange={handleChange} className={styles.nameInput} placeholder=" ."/>
    </form>
  );
}
